const img2 =
    "http://hlwtest.deskpro.cn/ljyy/choptest/src/public/images/login-box-img.png";
function downloadBase64Img(base64URL, fileName) {
    // a
    const a = document.createElement("a");
    //  a  download 
    a.download = fileName || "image";
    //  Blob  base64  MIME 
    const mimeType = base64URL.match(/:(.*?);/)[1];
    //  base64 
    const byteCharacters = atob(base64URL.split(",")[1]);
    const byteNumbers = new Array(byteCharacters.length);
    //  Uint8Array 
    for (let i = 0; i < byteCharacters.length; i++) {
        byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    const byteArray = new Uint8Array(byteNumbers);
    //  Blob 
    const blob = new Blob([byteArray], { type: mimeType });
    //  Blob  URL  a  href 
    a.href = URL.createObjectURL(blob);
    // a body 
    document.body.appendChild(a);
    a.click();
    //  a  body 
    document.body.removeChild(a);
}

export default {
    data() {
        return {
            instance: null,
            img1,
            img2,
        };
    },
    mounted() {
        this.instance = new ImageEditor(
            document.querySelector("#tui-image-editor"),
            {
                includeUI: {
                    loadImage: {
                        path: img1,
                        name: "image",
                    },
                    initMenu: "draw",
                    menuBarPosition: "bottom",
                    locale: locale_zh, // 
                    theme: customTheme,
                },
            }
        );
    },
    methods: {
        download() {
            console.log(this.instance.toDataURL());
            downloadBase64Img(this.instance.toDataURL(), "");
        },
        set(src) {
            this.instance = null;
            const innerHtml = document.querySelector("#edit-box");
            innerHtml.innerHTML = "";
            const newDom = document.createElement("div");
            newDom.id = "tui-image-editor";
            innerHtml.append(newDom);
            this.$nextTick(() => {
                this.instance = new ImageEditor(
                    document.querySelector("#tui-image-editor"),
                    {
                        includeUI: {
                            loadImage: {
                                path: src,
                                name: "image",
                            },
                            initMenu: "draw",
                            menuBarPosition: "bottom",
                            locale: locale_zh, // 
                            theme: customTheme,
                        },
                    }
                );
            });
        },
    },
};
